import React from "react";
import { Menu } from "antd";
import { Link, useRouteMatch } from "react-router-dom";
import {
  SnippetsOutlined,
  UserOutlined,
  HomeOutlined,
} from "@ant-design/icons";

const HeaderMenu = () => {
  const isMembersPath = useRouteMatch("/members");
  const isMediaPath = useRouteMatch("/media");

  const getSelectedKeys = () => {
    if (isMembersPath) return isMembersPath.path;
    if (isMediaPath) return isMediaPath.path;
    return "/portal";
  };

  return (
    <Menu mode="horizontal" selectedKeys={[getSelectedKeys()]}>
      <Menu.Item key="/portal" icon={<HomeOutlined />}>
        <Link to="/">База</Link>
      </Menu.Item>
      <Menu.Item key="/members" icon={<UserOutlined />}>
        <Link to="/members">Команда</Link>
      </Menu.Item>
      <Menu.Item key="/media" icon={<SnippetsOutlined />}>
        <Link to="/media">Фотогалерея</Link>
      </Menu.Item>
    </Menu>
  );
};

export default HeaderMenu;
