import React from "react";
import { Card, Space } from "antd";
import { Link } from "react-router-dom";

import membersCover from "../../assets/compressed/media-min.jpg";
import mediaCover from "../../assets/compressed/members-min.jpg";

const { Meta } = Card;

const PortalContent = () => (
  <Space
    style={{ width: "100%" }}
    align="center"
    direction="vertical"
    size="small"
  >
    <Link to="/members">
      <Card
        style={{ width: 345 }}
        hoverable
        cover={<img alt="members" src={membersCover} />}
      >
        <Meta title="Команда" description="Состав команды Tier Nova" />
      </Card>
    </Link>

    <Link to="/media">
      <Card
        style={{ width: 345 }}
        hoverable
        cover={<img alt="officers" src={mediaCover} />}
      >
        <Meta
          title="Фотогалерея"
          description="Хранилище фотографий"
        />
      </Card>
    </Link>
  </Space>
);

export default PortalContent;
