import React from "react";
import { Space } from "antd";

import neal from "../../assets/compressed/neal-min.jpg";
import krieg from "../../assets/compressed/krieg-min.jpg";
import merk from "../../assets/compressed/val-min.jpg";
import alex from "../../assets/compressed/kir-min.jpg";
import kir from "../../assets/compressed/alex-min.jpg";
import blackwood from "../../assets/compressed/blackwood-min.jpg";

import MemberCard from "./MemberCard";

const DESCRIPTIONS = {
  krieg: "Карим",
  neal: "Дмитрий",
  merk: "Валентин",
  alex: "Алексей",
  kir: "Кирилл",
  blackwood: "Дмитрий",
};

const MembersList = () => (
  <Space align="center" direction="vertical" style={{ width: "100%" }}>
    <MemberCard title="Neal" src={neal} description={DESCRIPTIONS.neal} />
    <MemberCard title="Merk" src={merk} description={DESCRIPTIONS.merk} />
    <MemberCard title="Krieg" src={krieg} description={DESCRIPTIONS.krieg} />
    <MemberCard
      title={"Blackwood"}
      src={blackwood}
      description={DESCRIPTIONS.blackwood}
    />
    <MemberCard title={"Кирилл"} src={kir} description={DESCRIPTIONS.kir} />
    <MemberCard title={"Алексей"} src={alex} description={DESCRIPTIONS.alex} />
  </Space>
);

export default MembersList;
