import React from "react";
import { Card } from "antd";

const { Meta } = Card;

const MemberCard = ({ src, title, description }) => {
  return (
    <Card
      title={title}
      style={{ minWidth: 300, maxWidth: 500 }}
      cover={<img alt="members" src={src} />}
    >
      <Meta description={description} />
    </Card>
  );
};

export default MemberCard;
